.main-body {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #fff;
    overflow: hidden;
    font-size: 1.125rem;
    font-family: STFangSong, Helvetica, Arial, Vernada, Tahoma, STXihei, 'Microsoft YaHei', 'Songti SC', SimSun, Heiti,
        sans-serif
}
.main-body .navigation-box {
    height: 50px;
    color: #777;
    border-bottom: 1px solid #e8e8e8;
    -webkit-box-shadow: 0 2px 8px #f0f1f2;
            box-shadow: 0 2px 8px #f0f1f2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between
}
.main-body .navigation-box a {
    color: #777;
    text-decoration: none;
    cursor: pointer;
}
.main-body .navigation-box .blog-logo {
    font-size: 1.5rem;
    line-height: 3.125rem;
    text-align: center;
    padding: 0 20px
}
.main-body .navigation-box .blog-logo .iconjidan {
    font-size: 1.5rem;
}
.main-body .navigation-box .nav-area {
    padding: 0 20px
}
.main-body .navigation-box .nav-area .nav-item {
    display: inline-block;
    height: 50px;
    line-height: 3.125rem;
    text-align: center;
    font-size: 1.25rem;
    padding: 0 10px;
}
.main-body .navigation-box .nav-area .nav-item:hover {
    background-color: #f5f5f5;
}
.main-body .content-box {
    height: calc(100% - 50px);
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
